import React from 'react';
import { Box, Flex, Heading, Link, Button } from '@chakra-ui/react';
import { NavLink } from 'react-router-dom';
import Sidebar from './Sidebar';

const Header = ({ isLoggedIn, username, onLogout }) => {
  return (
    <>
      <Box bg="blue.800" p={10}>
        <Flex align="center" justify={isLoggedIn ? "center" : "space-between"}>
          <Heading color="white">LMS System</Heading>

          {/* Conditionally show Register and Login links if not logged in */}
          {!isLoggedIn && (
            <Flex>
              <Link as={NavLink} to="/register" color="white" mx={4}>
                Register
              </Link>
              <Link as={NavLink} to="/login" color="white" mx={4}>
                Login
              </Link>
            </Flex>
          )}

          {/* Conditionally show Logout button and username if logged in */}
          {isLoggedIn && (
            <Flex align="center">
              <Heading size="sm" color="white" mx={4}>Hello, {username}</Heading>
              <Button colorScheme="red" onClick={onLogout}>
                Logout
              </Button>
            </Flex>
          )}
        </Flex>
      </Box>

      {/* Conditionally render the Sidebar component after login */}
      {isLoggedIn && <Sidebar />}
    </>
  );
};

export default Header;
