import React, { useState } from 'react';
import { Flex, Box, Button, Input, Stack, Heading, Text } from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

function Login({ onLogin }) {  // Receive onLogin as a prop
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleLogin = async () => {
    try {
      const response = await axios.post('/api/login', {
        username,
        password,
      });

      if (response.status === 200) {
        // Pass username and token to onLogin handler
        onLogin(response.data.username, response.data.token);

        // Redirect to dashboard or any other page
        navigate('/dashboard');
      }
    } catch (error) {
      if (error.response && error.response.status === 401) {
        setError('Invalid username or password');
      } else {
        setError('An error occurred. Please try again.');
      }
    }
  };

  return (
    <Flex align="center" justify="center" minHeight="83vh" bg="gray.100">
      <Box p={10} maxWidth="400px" borderWidth={2} borderRadius={20} boxShadow="lg">
        <Heading as="h2" size="lg" textAlign="center" mb={10}>
          Login
        </Heading>

        {/* Show error message if login fails */}
        {error && <Text color="red.500" mb={4}>{error}</Text>}

        <Stack spacing={4}>
          <Input
            placeholder="Username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}  // Update username state
          />
          <Input
            placeholder="Password"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}  // Update password state
          />
          <Button colorScheme="blue" variant="solid" width="full" onClick={handleLogin}>
            Login
          </Button>
        </Stack>
      </Box>
    </Flex>
  );
}

export default Login;
