import React from 'react';
import { Box, Flex, Text } from '@chakra-ui/react';

function Dashboard() {
  return (
    <Flex direction="column" align="center" justify="center" minHeight="82vh">
      <Box bg="blue.500" p={6} color="white" borderRadius="md" shadow="md">
        <Text fontSize="2xl">Welcome to the Dashboard</Text>
      </Box>
    </Flex>
  );
}

export default Dashboard;
