import React from 'react';
import { Box, VStack, Link, useDisclosure, IconButton, Collapse } from '@chakra-ui/react';
import { HamburgerIcon, CloseIcon } from '@chakra-ui/icons';
import { NavLink } from 'react-router-dom';

const Sidebar = () => {
  const { isOpen, onToggle } = useDisclosure();

  return (
    <Box width={isOpen ? "250px" : "80px"} transition="width 0.2s" bg="blue.800" height="100vh" position="fixed">
      <IconButton
        aria-label="Toggle"
        icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
        onClick={onToggle}
        colorScheme="teal"
        variant="outline"
        margin="20px"
      />
      <Collapse in={isOpen}>
        <VStack align="left" spacing={5} mt={15} p={20}>
          <Link as={NavLink} to="/" color="white">
            Dashboard
          </Link>
          <Link as={NavLink} to="/assignments" color="white">
            View Assignments
          </Link>
          <Link as={NavLink} to="/feedback" color="white">
            Upload Feedback
          </Link>
          <Link as={NavLink} to="/materials" color="white">
            Upload Course Materials
          </Link>
          <Link as={NavLink} to="/queries" color="white">
            View Queries
          </Link>
        </VStack>
      </Collapse>
    </Box>
  );
};

export default Sidebar;
